import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import { useRenderProps } from '@vl/hooks/useRenderProps';

import CategoryItemX from '@uz/unitz-components-web/CategoryItemX';
import FlatList from '@uz/unitz-components-web/FlatList';
import { Row, Col } from 'antd';

const renderItem = useRenderProps(
  ({ item, index }) => {
    return (
      <Col lg={12} md={24} sm={24} className="mb-4 sm:mb-0 border-b sm:border-b-0">
        <DIV forceCtx>
          {ctx.debug(() => {
            ctx.set('@item', item);
            ctx.set('@index', index);
          })}
          <CategoryItemX />
        </DIV>
      </Col>
    );
  },
  ({ item, index }) => [index, _.get(item, 'id')]
);

const View8 = () => (
  <DIV className="component">
    <div>
      <h2 className="font-semibold text-main text-xl text-center md:text-left lg:text-left lg:text-2xl mb-8 md:mb-6">
        {ctx.apply('i18n.t', 'Category.highlightCategory')}
      </h2>
      <FlatList
        as={Row}
        gutter={24}
        data={ctx.get('categories', [])}
        renderItem={renderItem}
        keyExtractor={(item, index) => `${_.get(item, 'id')}_${index}`}
      />
    </div>
  </DIV>
);

export default View8;
