import { bindings, hook } from '@vl/redata';
import useLoadingModel from '@vl/hooks/useLoadingModel';
import React from 'react';
import _ from 'lodash';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            loadingModel: hook(useLoadingModel(() => ({ fetchProfile: true }))),
            categories: hook((ctx) => {
              const categories = ctx.apply('ctf.buildCategoryHierarchy');
              const optimizeFilter = React.useMemo(
                () => _.filter(categories, (item) => {
                  return !_.get(item, 'parent');
                }),
                [categories]
              );
              return optimizeFilter;
            }),

            sectionData: hook((ctx) => {
              const section = ctx.apply('ctf.findSection', { name: 'Categories' });
              return {
                section,
              };
            }),
          },
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
