import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import { Link } from '@uz/unitz-components-web/Link';

const View8 = () => (
  <DIV className="component">
    <div className="py-6 lg:py-8 px-4 lg:px-6 rounded-lg bg-background1">
      <h2 className="font-semibold text-main text-center lg:text-left text-xl mb-4 md:mb-6">
        {ctx.apply('i18n.t', 'Category.moreCategories')}
      </h2>
      {ctx.get('categories', []).map((cat) => (
        <div className="mb-4" key={_.get(cat, 'id')}>
          <Link className="text-base font-normal" to={ctx.apply('routeStore.toUrl', 'category', cat)}>
            {_.get(cat, 'displayName', '')}
          </Link>
        </div>
      ))}
    </div>
  </DIV>
);

export default View8;
